import PermutationLister from './PermutationLister';

export default class CircularPermutationLister {
  private _specifiedCount: number;

  constructor() {
    this._specifiedCount = -1;
  }

  public generateLists(
    items: string[],
    specifiedCount: number = this._specifiedCount
  ): string[][] {
    let permutations: string[][] = [];
    let testLists: string[][] = [];
    for (let i = 0; i <= items.length - specifiedCount; i++) {
      permutations = (new PermutationLister()).generateLists(
        items.slice(i + 1),
        specifiedCount - 1
      );
      if (0 < permutations.length) {
        permutations = permutations.filter((permutation: string[]): any => {
          return permutation.unshift(items[i]);
        });
      } else {
        permutations.push([items[i], ])
      }
      testLists = testLists.concat(permutations);
    }
    return this.filterLists(testLists);
  }

  public filterLists(testLists: string[][]): string[][] {
    let lists: string[][] = [];
    for (let i: number = testLists.length - 1; 0 < i ; i--) {
      let doubleList: string[] = testLists[i].concat(testLists[i]);
      let sameListExists: boolean = false;
      doubleListTester:
      for (let j: number = 1; j < testLists[i].length; j++) {
        for (let k: number = 0; k < i; k++) {
          if (JSON.stringify(testLists[k])
            === JSON.stringify(doubleList.slice(j, j + testLists[i].length))) {
            sameListExists = true;
            break doubleListTester;
          }
        }
      }
      if (sameListExists === false) {
        lists.unshift(testLists[i]);
      }
    }
    lists.unshift(testLists[0]);
    return lists;
  }
}